import * as Styled from './styles';
import {ArticleHeader, ArticleProps} from '../ArticleHeader';
import {HtmlComponent} from '../HtmlContent';

export type PostProps = ArticleHeaderProps & {
  content: string;
};

export const Post = ({
  title,
  author,
  categories,
  content,
  cover,
  createdAt,
  excerpt,
  id
 }: PostProps) => {
  return (
    <Styled.Wrapper>
    <PostContainer size="max">
      <ArticleHeader author={author} categories={categories}
        title={title} excerpt={excerpt} id={id} cover={cover}
        createdAt={createdAt} />
     </PostContainer>
     <PostContainer size="content">
        <HtmlComponent html={content}/>
      </PostContainer>
    </Styled.Wrapper>
  );
};
